import Helper from '@/function.js';
export default {

	data() {

		return {
			level: 1,
			token: "",
			list: [],
			pageIndex: 1,
			pageSize: 15,
			teamInfo: {},
			total: 0,
			teamTotal:"--",
			info:{},
			teamPayTotal:0,
		}
	},
	onLoad: function() {
		this.token = uni.getStorageSync('token');
		this.getTeamCount();
		this.getTeamTotalCount();
		this.getOneTeam();
		this.getInfo();
	},

	onReachBottom: function() {
		if (this.list.length == this.total) {
			return;
		}

		this.pageIndex++;
		this.getOneTeam();
	},

	onPullDownRefresh: function() {
		this.pageIndex = 1;
		this.list = [];
		this.getOneTeam();
	},

	methods: {
		getInfo() {
			var _this = this;
			uni.showLoading({
				title: "请稍候..."
			})
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetInfo',
				data: "",
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);
		
					if (Helper.CheckData(res)) {
						_this.info = res.data.data;
					} else {
		
					}
				},
				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
				}
		
		
			})
		},
		getTeamCount() {
			var _this = this;

			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTeamCount',
				data: "",
				method: 'GET',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {
						_this.teamInfo = res.data.data;
					} else {

					}
				},
				fail: function() {
					//uni.hideLoading();
				},
				complete: function() {
					//uni.hideLoading();
				}


			})
		},
		getTeamTotalCount() {
			var _this = this;
		
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTeamTotalCount',
				data: "",
				method: 'GET',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);
		
					if (Helper.CheckData(res)) {
						_this.teamTotal = res.data.data.total - 1;
						_this.teamPayTotal = res.data.data.teamPayTotal;
					} else {
		
					}
				},
				fail: function() {
					//uni.hideLoading();
				},
				complete: function() {
					//uni.hideLoading();
				}
		
		
			})
		},
		getOneTeam() {
			var _this = this;
			var params = {
				pageIndex: _this.pageIndex,
				pageSize: _this.pageSize,
				level: _this.level
			}
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTeam',
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);

					if (Helper.CheckData(res)) {

						for (var i = 0; i < res.data.list.length; i++) {
							var rank = res.data.list[i].rank;
							var type = res.data.list[i].memberType;
							var typeStr = "";
							var rankStr = "";
							switch (type) {
								case 0:
									typeStr = "普通用户";
									break;
								case 1:
									typeStr = "经销商";
									break;
								case 2:
									typeStr = "代理商";
									break;
								case 3:
									typeStr = "总代";
									break;

							}


							switch (rank) {
								case 0:
									rankStr = "普通";
									break;
								case 1:
									rankStr = "网红";
									break;
								case 2:
									rankStr = "达人";
									break;
								case 3:
									rankStr = "明星";
									break;
								case 4:
									typeStr = "巨星";
									break;
							}
							res.data.list[i].rankStr = rankStr + "-" + typeStr;
						}
						_this.list = _this.list.concat(res.data.list);
						_this.total = res.data.total;
					} else {

					}
				},
				fail: function() {
					//uni.hideLoading();
				},
				complete: function() {
					//uni.hideLoading();
					uni.stopPullDownRefresh()
				}


			})
		},
		btnClick: function(index) {
			console.log(index);
			if (this.level == index) {
				return;
			}

			this.level = index;
			this.pageIndex = 1;
			this.list = [];
			this.getOneTeam()
		},
		bank() {

			uni.navigateBack({
				delta: 1
			})
		},



	}

}
